body {
    background: black;
}

svg path {
    fill: blue;
    transition: all ease 0.3s;
}

.vlb-single-region:hover path {
    filter: brightness(65%);
    transition: all ease 0.3s;
}

.vlb-map {
    height: 100%;
    left: 0;
    pointer-events: all;
    position: relative;
    top: 0;
    width: 100%;
}

.vlb-map-stroke {
    stroke: var(--szn-vlb--color-background-primary, #fff);
}

#body {
    margin-top: 100px;
    margin-left: 200px;
    margin-right: 200px;
}

.vlb-val {
    color: red;
    font-weight: bold;
    font-size: 45px;
}

.vlb-nadpis {
    color: #555555;
    font-weight: bold;
    font-size: 20px;
}

#mapacr {
    max-width: 50%;
    min-width: 400px;
}

.info-col {
    margin: 0 20px 0 20px;
}

#loadingScreen {
    position:fixed;
    padding:0;
    margin:0;
    top:0;
    left:0;
    width: 100%;
    height: 100%;
    background:rgba(0,0,0,0.3);
    z-index: 3000;
}

#loadingScreen .spinner-border {
    width: 5rem;
    height: 5rem;
    position: absolute;
    top: 50%;
    right: 50%;
}

#countdown {
    margin-top: 100px;
}


.autocomplete {
    background: white;
    z-index: 1000;
    font: 14px/22px "-apple-system", BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
    overflow: auto;
    box-sizing: border-box;
    border: 1px solid rgba(50, 50, 50, 0.6);
}

.autocomplete * {
    font: inherit;
}

.autocomplete > div {
    padding: 0 4px;
}

.autocomplete .group {
    background: #eee;
}

.autocomplete > div:hover:not(.group),
.autocomplete > div.selected {
    background: #81ca91;
    cursor: pointer;
}